.backToList{
    background-image: url("../../assets/images/Vector.png");
    background-size: 100%;
    position: absolute;
    width: 50px;
    height: 75px;
    left: 1200px;
    top: -170px;
    cursor: pointer;
}

.backToListArrow{
    background-image: url("../../assets/images/Vector\(1\).png");
    background-size: 100%;
    position: absolute;
    width: 42px;
    height: 40px;
    left: 1240px;
    top: -160px;
    cursor: pointer;
}

.addList{
    position: absolute;
width: 104px;
height: 104px;
left: 700px;
top: 900px;
border-radius: 50%;
background: #FFFFFF;
box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.addListLine{
    position: absolute;
width: 78px;
height: 0px;
left: 715px;
top: 950px;
background-color: #FF9900;
border: 3px solid #FF9900;
transform: rotate(90deg);
}

.addListLine2{
    position: absolute;
width: 78px;
height: 0px;
left: 715px;
top: 950px;
background-color: #FF9900;
border: 3px solid #FF9900;
transform: rotate(-180deg);
}

.search{
    position: absolute;
    width: 400px;
    height: 50px;
    left: 90px;
    top: 80px;
    
    background: #FFFFFF;
    border-radius: 8px;

font-family: Roboto;
font-style: normal;
font-weight: 300;
font-size: 24px;
line-height: 28px;

color: #000000;
}

.sort{
    position: absolute;
width: 200px;
height: 50px;
left: 600px;
top: 80px;

background: #FFFFFF;
border-radius: 8px;

font-family: Roboto;
font-style: normal;
font-weight: 300;
font-size: 24px;
line-height: 28px;

color: #000000;
}